"use client";

import { useState } from "react";

export default function Home() {
  const [code, setCode] = useState("");
  const [view, setView] = useState("execution");

  const [execution, setExecution] = useState("");
  const [asm, setAsm] = useState("");
  const [hex, setHex] = useState("");
  const [bin, setBin] = useState("");

  const runCode = async () => {
    const res = await fetch("/api/parse", {
      method: "POST",
      body: code
    });

    const data = await res.json();
    setExecution(data.execution || "");
    setAsm(data.asm || "");
    setHex(data.hex || "");
    setBin(data.bin || "");
  };

  const output = {
    execution,
    asm,
    hex,
    bin,
  };

  return (
    <main className="min-h-screen bg-gray-900 text-white p-6">
      <h1 className="text-3xl font-bold mb-6">Mini Compiler IDE</h1>

      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">

        {/* -------- CODE EDITOR -------- */}
        <div className="bg-gray-800 rounded-lg border border-gray-700 p-4">
          <h2 className="text-xl font-semibold mb-2">Code Editor</h2>

          <textarea
            value={code}
            onChange={(e) => setCode(e.target.value)}
            className="w-full h-80 bg-gray-900 text-green-400 p-3 rounded-lg font-mono outline-none border border-gray-700"
            placeholder="Type your source code here..."
          />

          <button
            onClick={runCode}
            className="mt-4 px-6 py-2 bg-blue-600 hover:bg-blue-700 rounded-lg font-semibold"
          >
            Run
          </button>
        </div>

        {/* -------- OUTPUT PANEL -------- */}
        <div className="bg-gray-800 rounded-lg border border-gray-700 p-4">

          <h2 className="text-xl font-semibold mb-3">Output</h2>

          {/* Tabs */}
          <div className="flex gap-3 mb-4">
            {["execution", "asm", "hex", "bin"].map((t) => (
              <button
                key={t}
                onClick={() => setView(t)}
                className={`px-4 py-2 rounded-lg capitalize ${
                  view === t ? "bg-blue-600" : "bg-gray-700"
                }`}
              >
                {t.replace("asm", "assembly")}
              </button>
            ))}
          </div>

          {/* Output window */}
          <pre className="w-full h-80 bg-gray-900 text-yellow-300 p-3 rounded-lg overflow-auto font-mono text-sm">
            {output[view] || "No output"}
          </pre>

        </div>
      </div>
    </main>
  );
}
